<template>
  <div class="banner">
    <img class="logo" :src="logo" alt="banner" />
  </div>
</template>

<script>
import logo from '@/assets/img/logo.png'

export default {
  data() {
    return {
      logo
    }
  }
}
</script>

<style lang="scss">
.banner {
  text-align: center;
  .logo {
    height: auto;
    width: 100%;
    max-width: 300px;
    max-height: 200px;
  }
}
</style>



