<template>
  <div id="body">
    <el-container>
      <!-- 顶栏容器 -->
      <el-header ref="head">
        <div class="header-container">
          <a href>
            <img src="@/assets/img/weindex.png" class="header-logo" alt="banner" />
          </a>
          <div style="flex: 1"></div>
          <router-link to="/login">
            <el-avatar
              style="margin: 0 5px; line-height: 50px"
              :size="50"
              :src="isLogin ? require('@/assets/img/avatar.jpeg') : ''"
            >登录</el-avatar>
          </router-link>
          <el-button @click="drawer = true" type="primary" icon="el-icon-setting">所有网站</el-button>
        </div>
      </el-header>
      <!-- 侧边栏容器 -->
      <el-aside ref="side">
        <el-drawer title="我是标题" :visible.sync="drawer">
          <DrawerBlock></DrawerBlock>
        </el-drawer>
      </el-aside>
      <!-- 主要区域容器 -->
      <el-main ref="main" v-bind:style="{ minHeight: height + 'px' }">
        <el-row>
          <el-col
            :lg="{ span: 16, offset: 4 }"
            :md="{ span: 18, offset: 3 }"
            :sm="{ span: 20, offset: 2 }"
          >
            <Banner />
          </el-col>
          <el-col
            :lg="{ span: 16, offset: 4 }"
            :md="{ span: 18, offset: 3 }"
            :sm="{ span: 20, offset: 2 }"
          >
            <Search class="search-container" />
          </el-col>

          <el-col
            :lg="{ span: 16, offset: 4 }"
            :md="{ span: 18, offset: 3 }"
            :sm="{ span: 20, offset: 2 }"
          >
            <WebApp />
          </el-col>
        </el-row>
      </el-main>
      <!-- 底栏容器 -->
      <el-footer height="35px" ref="footer">
        <p @click="$router.push({ name: 'aboutus' })" target="_blank">微意首页 · 关于我们</p>
        <!-- <a href="https://beian.miit.gov.cn/" target="_blank">Copyright © 2021 微意首页 粤ICP备XXXXX号</a> -->
      </el-footer>
    </el-container>
  </div>
</template>

<script>
import Search from '@/components/index/Search.vue'
import Banner from '@/components/index/Banner.vue'
import WebApp from '@/components/index/WebApp.vue'
import DrawerBlock from '@/components/index/DrawerBlock.vue' // 右侧边栏
export default {
  name: 'WeIndex',
  data() {
    return {
      height: 0,
      search: '',
      select: '',
      scrollElement: '',
      drawer: false,
      isLogin: false
    }
  },
  components: {
    Search,
    Banner,
    WebApp,
    DrawerBlock
  },
  methods: {
    // 重置窗口可视高度
    resetDocumentClientHeight() {
      this.documentClientHeight = document.documentElement['clientHeight']
      window.onresize = () => {
        this.documentClientHeight = document.documentElement['clientHeight']
      }
    },

    // 点击登录
    login() {},

    // 获取当前管理员信息
    getUserInfo() {
      this.$http({
        url: this.$http.adornUrl('/sys/user/info'),
        method: 'get',
        params: this.$http.adornParams()
      }).then(({ data }) => {
        if (data && data.code === 0) {
          this.loading = false
          this.userId = data.user.userId
          this.userName = data.user.username
          this.$notify({
            title: 'It works!',
            type: 'success',
            message:
              "We've laid the ground work for you. It's time for you to build something epic!",
            duration: 5000
          })
        }
      })
    }
  },
  mounted() {
    console.log(encodeURI('1dasd'))
    // 动态设置内容高度 让footer始终居底   header+footer的高度是35
    this.height =
      document.documentElement.clientHeight -
      this.$refs.head.$el.clientHeight -
      this.$refs.side.$el.clientHeight -
      this.$refs.footer.$el.clientHeight // 监听浏览器窗口变化
    window.onresize = () => {
      this.height =
        document.documentElement.clientHeight -
        this.$refs.head.$el.clientHeight -
        this.$refs.side.$el.clientHeight -
        this.$refs.footer.$el.clientHeight -
        35
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->

<style lang="scss">
body {
  width: 100%;
}
.el-header,
.el-footer {
  color: #333;
  &.el-footer {
    font-size: 13px;
    text-align: center;
    & > a {
      text-decoration: none;
      color: #666;
    }
  }
}

.header-container {
  display: flex;
  box-sizing: border-box;
  padding: 2px 5px;
  background: #fff;
  img {
    height: 50px;
  }
  .el-button {
    margin: 0 0 0 15px;
  }
}

.search-container {
  padding-bottom: 15px;
}
</style>



