<template>
  <div style="margin-top: 15px" @keyup.enter="search" onload="searchbox.focus()">
    <el-input
      id="searchbox"
      placeholder="请输入内容"
      v-model="query"
      @change="setQuery"
      class="input-with-select"
    >
      <el-select
        v-model="searchEngineIndex"
        slot="prepend"
        placeholder="请选择"
        class="search-options"
      >
        <el-option
          v-for="item in searchEngine"
          :label="item.label"
          :value="item.value"
          :key="item.label"
        ></el-option>
      </el-select>
      <el-button slot="append" icon="el-icon-search" @click="search" class="search-btn"></el-button>
    </el-input>
  </div>
</template>

<script>
import { searchEngine } from '@/utils/default-web'
export default {
  data() {
    return {
      query: '',
      searchEngine,
      searchEngineIndex: 0
    }
  },
  methods: {
    search() {
      let query = this.query
      if (this.searchEngine[this.searchEngineIndex].label === '影视资源') {
        query = encodeURI(query).replace(/%/img, '_') + '.html'
      }
      window.open(this.searchEngine[this.searchEngineIndex].url + query)
    },
    setQuery() {
      this.query = this.query
    }
  }
}
</script>

<style>
.el-select .el-input {
  width: 130px;
}
.input-with-select .el-input-group__prepend {
  background-color: #fff;
}
.search-options {
  background: #00e6ff;
  color: white;
}
.search-btn {
  color: #fff !important;
  background: #00e6ff !important;
}
</style>



