<template>
  <div>
    <el-row :gutter="10" class="app-panel-container">
      <el-col
        :sm="10"
        :md="8"
        :lg="6"
        v-for="item in data"
        v-bind:key="item.name"
        class="app-panel-item"
      >
        <a :href="item.url" target="_blank">
          <el-card :body-style="{ padding: '0px' }" class="appList">
            <i class="el-icon-edit" @click.stop="editApp"></i>
            <img class="web-icon" :src="item.icon" />
            <span>{{ item.name }}</span>
          </el-card>
        </a>
      </el-col>
    </el-row>
  </div>
</template>

<script>
export default {
  props: ['data'],
  data() {
    return {}
  },
  methods: {
    /**
     * 修改app数据
     */
    editApp(e) {
      // 阻止a标签默认行为
      e.preventDefault()
      this.$notify({
        title: 'It works!',
        type: 'success',
        message:
          "We've laid the ground work for you. It's time for you to build something epic!",
        duration: 5000
      })
    }
  }
}
</script>

<style lang="scss">
@mixin setBackground($value) {
  background-color: $value;
  color: white;
}

.app-panel-container {
  padding: 10px;
  background: #f6f6f6;

  .app-panel-item {
    margin-bottom: 20px;

    & > a {
      font-size: 14px;
      text-decoration: none;
      text-align: center;

      .el-card {
        margin: 0 auto;
        position: relative;
        .el-icon-edit {
          display: none;
          padding: 5px;
          font-size: 15px;
          right: 0;
          top: 0;
          color: #888;
          background: #fff;
          position: absolute;
        }
        .web-icon {
          border-radius: 5px;
        }
      }
      .el-card:hover .el-icon-edit {
        display: block;
      }

      .appList:hover {
        @include setBackground(#00e6ff);
      }
      .appList {
        background: #fff;
        width: 120px;
        padding: 5px 20px;
        box-sizing: border-box;
        border-radius: 5px;
        img {
          height: 60px;
          padding: 0 5px;
        }
        span {
          display: inline-block;
          white-space: nowrap;
          width: 100%;
          overflow: hidden;
          text-overflow: ellipsis;
        }
      }
    }
  }
}
</style>



