<template>
  <div>
    <el-tabs type="border-card" addable>
      <el-tab-pane
        v-for="item in defaultWebApp"
        :label="item.title"
        :key="item.title"
      >
        <Block :data="item.list"></Block>
      </el-tab-pane>
    </el-tabs>
  </div>
</template>

<script>
import { defaultWebApp } from '@/utils/default-web'
import Block from '@/components/index/Block.vue'

export default {
  components: { Block },
  data() {
    return {
      defaultWebApp
    }
  }
}
</script>



